import React, { Component, PropTypes } from 'react'
import Footer from '../components/Footer'
import Header from '../components/Header'
import Img from 'gatsby-image'
import Link from 'gatsby-link'
import Overview from '../components/Overview'
import Preview from '../components/Preview'
import animation from '../style/animation'
import background from '../style/background'
import classNames from 'classnames'
import data from '../data/work'
import grid from '../style/grid'
import layout from '../style/layout'
import text from '../style/type'
import { css } from 'glamor'
import { navigateTo } from 'gatsby-link'

class SecondPage extends Component {
  constructor(props) {
    super(props)
    this.handleOnClickLeave = this.handleOnClickLeave.bind(this)
    this.state = {
      animating: true,
      leaving: false,
      entering: true,
    }
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({ animating: false, entering: false })
    }, 1400)
  }

  handleOnClickLeave(loc) {
    console.log(loc)
    this.setState({ leaving: true })
    setTimeout(() => {
      navigateTo(loc)
    }, 450)
  }

  render() {
    const { ht100, leftCol, rightColAuto, img } = layout
    const { link, overview } = text
    const { solid } = background

    return (
      <div {...ht100}>
        <section {...leftCol}>
          <div
            className={classNames({
              [animation.pageSwipe]: true,
              [animation.swipeIntro]: this.state.entering,
              [animation.swipeLeave]: this.state.leaving,
            })}
          />
          <div {...solid}>
            <Img className={img} sizes={this.props.data.imgOne.sizes} />
            <Img className={img} sizes={this.props.data.imgTwo.sizes} />
            <Img className={img} sizes={this.props.data.imgThree.sizes} />
          </div>
        </section>
        <main {...rightColAuto}>
          <Header />
          <Overview
            data={data[1]}
            entering={this.state.entering}
            leaving={this.state.leaving}
          />
          <Footer
            animating
            handleOnClick={this.handleOnClickLeave}
            nextSlug={data[1].next}
          />
        </main>
      </div>
    )
  }
}

SecondPage.PropTypes = {
  data: PropTypes.object,
  leaving: PropTypes.bool,
}

export default SecondPage

export const query = graphql`
  query OssPageQuery {
    imgOne: imageSharp(id: { regex: "/work-oss-lander.png/" }) {
      sizes(maxWidth: 630) {
        ...GatsbyImageSharpSizes
      }
    }
    imgTwo: imageSharp(id: { regex: "/work-oss-webpack-dashboard.jpg/" }) {
      sizes(maxWidth: 630) {
        ...GatsbyImageSharpSizes
      }
    }
    imgThree: imageSharp(id: { regex: "/work-oss-style-guide.png/" }) {
      sizes(maxWidth: 630) {
        ...GatsbyImageSharpSizes
      }
    }
  }
`



